import { Component, OnInit, AfterViewInit } from '@angular/core';
import { CartService } from '../../../../services/cart/cart.service';
import { PromotionService } from '../../../../services/promotion/promotion.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { environment } from '../../../../../environments/environment';
import {
  isValid,
  isExpirationDateValid,
  isSecurityCodeValid,
  getCreditCardNameByNumber,
} from 'creditcard.js';

declare var Stripe;

@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.scss'],
})
export class CheckoutComponent implements OnInit {
  constructor(
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private cartService: CartService,
    private promotionService: PromotionService
  ) {}
  stripePublishablekey: any = environment.publishablekey;
  ngOnInit(): void {
    this.getProducts();
    this.calculateTotalCost();
  }

  products = [];
  totalCost = 0;
  code = [];

  getProducts() {
    this.products = this.cartService.getCart();
    console.log(this.cartService.getCart());
  }

  cardNumber;
  cardProvider = 'Visa';

  checkCreditCard() {
    console.log(isValid(this.cardNumber)); // returns true
    console.log(isExpirationDateValid('02', '2027')); // returns true
    console.log(isSecurityCodeValid(this.cardNumber, '250')); // returns true

    if (getCreditCardNameByNumber(this.cardNumber) != 'Credit card is invalid!') {
      console.log(getCreditCardNameByNumber(this.cardNumber));
      this.cardProvider = getCreditCardNameByNumber(this.cardNumber);
    }
    // returns 'Visa'
  }

  calculateTotalCost() {
    this.totalCost = 0;
    this.products.map((item) => {
      this.totalCost += item.numberOfDays * 0.2;
    });
  }

  //////////////////////////////////////// STRIPE ///////////////////////

  stripe: any = '';
  element: any = '';
  style: any = '';
  card: any = '';
  stripeRes = { status: 'not succeeded' };
  cashOnDeliveryRes = { status: 'not succeeded' };

  ngAfterViewInit() {
    this.stripe = Stripe(this.stripePublishablekey);
    this.element = this.stripe.elements();
    this.style = {
      base: {
        border: 'orange',
        color: '#32325d',
        fontFamily: 'Arial, sans-serif',
        fontSmoothing: 'antialiased',
        fontSize: '16px',
        '::placeholder': {
          color: '#32325d',
        },
      },
      invalid: {
        fontFamily: 'Arial, sans-serif',
        color: 'red',
        iconColor: '#fa755a',
      },
    };
    this.card = this.element.create('card', { style: this.style });
    this.card.mount('#card-element');

    // Handle real-time validation errors from the card Element.
    this.card.addEventListener('change', function (event) {
      var displayError = document.getElementById('card-errors');
      if (event.error) {
        displayError.textContent = event.error.message;
      } else {
        displayError.textContent = '';
      }
    });

    // Handle form submission.
    var form = document.getElementById('payment-form');
    form.addEventListener('submit', (event) => {
      if (this.totalCost < 0.7) {
        this.toastr.error('Amount must be greater than 70 cents', 'Error');
        return false;
      }
      event.preventDefault();
      this.spinner.show();
      this.stripe.createToken(this.card).then((result) => {
        if (result.error) {
          // Inform the user if there was an error.
          this.toastr.error(result.error.message, 'Error');
          this.spinner.hide();
          var errorElement = document.getElementById('card-errors');
          errorElement.textContent = result.error.message;
        } else {
          // Send the token to your server.
          // stripeTokenHandler(result.token);
          console.log(result.token);
          this.chargeStripe(result.token.id);
        }
      });
    });

    ////////////////////////////////////////////////////////////////////////////////
  }

  chargeStripe(token: any) {
    let items: any = JSON.stringify(this.products);
    items = JSON.parse(items);
    items.map((item) => {
      delete item.actualPrice;
      delete item.action;
      delete item.numberOfDays;
      delete item.productName;
      delete item.bundleName;
    });
    const amountPayable = (this.totalCost * 100).toString();
    const payload = {
      items,
      token,
      totalCost: parseInt(amountPayable, 10),
    };
    console.log(payload);
    if (payload.items[0].productCatalogId) {
      this.promotionService.createProductPromotion(payload).subscribe(
        (data) => {
          this.cartService.trash();
          this.products = [];
          this.totalCost = 0;
          // this.toastr.success(data.message, 'Success');
          this.spinner.hide();
        },
        (error) => {
          this.toastr.error(error.error.message, 'Error');
          this.spinner.hide();
        }
      );
    } else if (payload.items[0].bundleCatalogId) {
      this.promotionService.createBundlePromotion(payload).subscribe(
        (data) => {
          this.cartService.trash();
          this.products = [];
          this.totalCost = 0;
          // this.toastr.success(data.message, 'Success');
          this.spinner.hide();
        },
        (error) => {
          this.toastr.error(error.error.message, 'Error');
          this.spinner.hide();
        }
      );
    }
  }

  deleteItem(index) {
    this.cartService.deleteItem(index);
    this.calculateTotalCost();
  }
}
