import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class CartService {
  constructor() {}

  private cart = [];

  addProduct(product) {
    this.cart.push(product);
  }

  addBundle(product) {
    this.cart.push(product);
  }

  getCart() {
    return this.cart;
  }

  trash() {
    this.cart = [];
  }

  deleteItem(index) {
    this.cart.splice(index, 1);
  }
}
